import { Component, OnInit } from '@angular/core';
import { LocalerService } from 'src/app/core/service/localer/localer.service';
import { Router } from '@angular/router';
import { UserService } from 'src/app/core/user/user.service';
// import {SimpleTimer} from 'ng2-simple-timer';
@Component({
  selector: 'app-introduce',
  templateUrl: './introduce.component.html',
  styleUrls: ['./introduce.component.scss']
})
export class IntroduceComponent implements OnInit {
  unShow = true;
  link: any
  ctg: string;
  data = {
    id: 14,
    name: 'OSLA',
    img: 'assets/img/tkkd/4.jpg',
    price: 560,
    inventory: 10,
    description: 'Vin ung Natrol Biotin 10.000 mcg b sung thnh phn Biotin (cn gi l vitamin H) - vn c bit n l l vitamin quan trng cho tc v mng. Natrol Biotin 10000 mcg khng cha ph gia gy hi, khng nh hng ti h tiu ha, thch hp s dng cho c nam v n.',
    categoryId: 2
  }

  constructor(
    private localer: LocalerService,
    private route: Router,
    private userService: UserService
  ) { }

  getDetail(product) {
    this.localer.saveLocalStorage({ currentProduct: product }, 'DETAIL');
    this.userService.changeProduct(product)
    this.route.navigateByUrl('/detail/' + product.id)
  }
  ngOnInit(): void {
    if (this.data.categoryId == 1) {
      this.ctg = 'Thuc k n'
    } else if (this.data.categoryId == 2) {
      this.ctg = 'Thuc khng k n'
    } else {
      this.ctg = 'Thc phm chc nng'
    }
  }
  showMore() {
    this.unShow = false
  }
  onDestroy() {
    this.unShow = true
  }
}
